import instance from "../instance";
import site from "../mockData/site"; // Auxiliary method to simulate mock data. 

var mockInstance = function mockInstance(mockData) {
  var time = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 0;
  return new Promise(function (resolve) {
    setTimeout(function () {
      resolve(mockData);
    }, time);
  });
};

export default {
  /* =========================================== */

  /* Site Endpoints */

  /* =========================================== */
  // GET /sites
  // List of sites that are authorized for access by the provided API key.
  getSiteList: function getSiteList(cb) {
    mockInstance(site, 1000).then(function (data) {
      return cb(data);
    }); // instance().get('/api/sites')
    //   .then(res => cb(res.data))
    //   .catch(err => console.log(err))
  },
  // GET /sites/{site-id}
  // Retrieve metadata for a specific site
  getSite: function getSite(cb) {
    instance().get('/api/sites/:id').then(function (res) {
      return cb(res.data);
    }).catch(function (err) {
      return console.log(err);
    });
  }
};